import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError, Subject, BehaviorSubject, ReplaySubject } from 'rxjs';
import { catchError, retry } from 'rxjs/operators';

import { CookieService } from 'ngx-cookie-service';

@Injectable({
    providedIn: 'root',
})
export class RF {
    private apiUrl = 'https://reinforce.moe/api/v2';
    private token = '';
    private tokenInfo: any;
    loggedIn = false;

    isLoggedIn: ReplaySubject<boolean>;

    constructor(private http: HttpClient, private cookie: CookieService) { 
        this.isLoggedIn = new ReplaySubject(1);
        if (cookie.check('token')){
            // token exist
            this._post('/user/token', { token: this.cookie.get('token') })
            .then(res => {
                if (res.code === 200){
                    console.log("Init check, request success", res);
                    this.setToken(res.data);
                    this.isLoggedIn.next(true);
                }else{
                    this.isLoggedIn.next(false);
                    return Promise.reject(res);
                }
            })
            .catch(err => {
                console.log("Init check failed, delete cookie");
                this.deleteToken();
                this.isLoggedIn.next(false);
            })
        }else{
            this.isLoggedIn.next(false);
        }
    }
    public test() {
        return this._get('');
    }
    login(username: string, password: string, keepLogin: boolean){
        return this._post('/user/login', { username: username, password: password, keeplogin: keepLogin })
        .then((resp: any) => {
            if (resp.code === 200){
                this.setToken(resp.data);
                return resp;
            }else{
                return Promise.reject(resp);
            }
        })
    }
    logout(){
        if (this.loggedIn){
            return this._post('/user/logout', { token: this.token })
            .then(res => {
                this.cookie.delete('token');
                this.token = '';
                this.tokenInfo = {};
                this.loggedIn = false;
                this.isLoggedIn.next(false);
                return res;
            })
        }else{
            return Promise.reject();
        }
    }

    diaryGetToday(){
        return this._get('/diary/get/today', true);
    }
    diaryWrite(content: string){
        return this._post('/diary/write', { content: content });
    }

    private setToken(tokenInfo: any){
        this.cookie.set('token', tokenInfo.token, { expires: new Date(tokenInfo.expire * 1000), secure: false, path: '/', sameSite: 'Lax' });
        this.tokenInfo = tokenInfo;
        this.token = tokenInfo.token;
        this.loggedIn = true;
    }
    private updateTokenExpire(){
        this.tokenInfo.expire = Number(this.tokenInfo.valid_time) + (Date.now() / 1000 | 0);
        this.setToken(this.tokenInfo);
    }
    /**
     * Delete token cookie
     */
    private deleteToken(){
        this.cookie.set('token', '', { expires: new Date('Thu, 01 Jan 1970 00:00:01 GMT'), secure: false, path: '/', sameSite: 'Lax' });
    }

    private _get(path: string, authorizeRequired = false): Promise<any> {
        if (this.loggedIn){
            this.updateTokenExpire();
            return this.http.get(this.apiUrl + path, { headers: { token: this.token }}).toPromise<any>();
        }
        else if (authorizeRequired){
            return new Promise((resolve, reject) => {
                this.isLoggedIn.subscribe(result => {
                    this.http.get(this.apiUrl + path, { headers: { token: this.token }}).toPromise<any>()
                    .then(resolve)
                    .catch(reject);
                })
            })
        }
        else return this.http.get(this.apiUrl + path).toPromise<any>();
    }
    private _post(path: string, data: any, authorizeRequired = false): Promise<any> {
        if (this.loggedIn){
            this.updateTokenExpire();
            return this.http.post(this.apiUrl + path, data, { headers: { token: this.token }}).toPromise<any>();
        }
        else if (authorizeRequired){
            return new Promise((resolve, reject) => {
                this.isLoggedIn.subscribe(result => {
                    this.http.post(this.apiUrl + path, data, { headers: { token: this.token }}).toPromise<any>()
                    .then(resolve)
                    .catch(reject);
                })
            })
        }
        else return this.http.post(this.apiUrl + path, data).toPromise<any>();
    }
}