import { ChangeDetectorRef, Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { MediaMatcher } from '@angular/cdk/layout';
import { UIService } from './ui.service';
import { RF } from './rf.service';
import { MatSidenav } from '@angular/material/sidenav';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css']
})
export class AppComponent implements OnDestroy, OnInit {
    @ViewChild('sidenav') sidenav: MatSidenav;
    mobileQuery: MediaQueryList;

    private _mobileQueryListener: () => void;
    subscription: Subscription;
    loggedIn = true; // assume logged in so that the content wont show until result is confirmed to prevent screen flash
    
    constructor(
        changeDetectorRef: ChangeDetectorRef, 
        media: MediaMatcher, 
        public ui: UIService,
        private rf: RF, 
        private router: Router
        ) {
        this.mobileQuery = media.matchMedia('(max-width: 768px)');
        this._mobileQueryListener = () => changeDetectorRef.detectChanges();
        this.mobileQuery.addListener(this._mobileQueryListener);
    }
    ngOnDestroy(): void {
        this.mobileQuery.removeListener(this._mobileQueryListener);
        this.subscription.unsubscribe();
    }
    ngOnInit() {
        this.ui.toggleTheme();
        this.subscription = this.rf.isLoggedIn.subscribe(result => {
            if (result) {
                console.log("You are logged in");
                this.loggedIn = true;
                switch (this.ui.getViewingPage()) {
                    case 'diary': this.router.navigate(['/diary']); break;
                    case 'note': this.router.navigate(['/note']); break;
                }
            } else {
                console.log("Please login");
                this.loggedIn = false;
                this.router.navigate(['/']);
                this.openLoginDialog();
            }
        })
    }


    openLoginDialog() {
        this.ui.openLoginDialog()
        .afterClosed()
        .subscribe(data => {
            if (data.username && data.password) {
                this.rf.login(data.username, data.password, data.keepLogin)
                .then(res => {
                    console.log("App Component: Login success", res);
                    this.rf.isLoggedIn.next(true);
                })
                .catch(err => {
                    console.log(err);
                    this.ui.openInfoBar(err.error.msg, 'Got it');
                    this.rf.isLoggedIn.next(false);
                })
            } else {
                console.log("LoginDialog: Login canceled");
            }
        })
    }

    logout(){
        this.rf.logout()
        .then(res => {
            this.ui.openInfoBar("Successfully logged out", 'Got it');
            this.router.navigate(['/']);
        })
        .catch(err => { 
            // Not logged in
            this.ui.openInfoBar("You are not logged in", 'Got it');
        });
    }


    private swipeCoord?: [number, number];
    private swipeTime?: number;

    swipe(e: TouchEvent, when: string): void {
        if (this.ui.isInputting) return;
        let coord: [number, number] = [e.changedTouches[0].clientX, e.changedTouches[0].clientY];
        let time = new Date().getTime();

        if (when === 'start') {
            this.swipeCoord = coord;
            this.swipeTime = time;
        } else if (when === 'end') {
            let direction = [coord[0] - this.swipeCoord[0], coord[1] - this.swipeCoord[1]];
            let duration = time - this.swipeTime;

            if (duration < 1000 //
                && Math.abs(direction[0]) > 100 // Long enough
                && Math.abs(direction[0]) > Math.abs(direction[1] * 3)) { // Horizontal enough
                let swipe = direction[0] < 0 ? 'left' : 'right';
                // Do whatever you want with swipe
                if (swipe == 'right') {
                    this.sidenav.open();
                }
            }
        }
    }
}
